<style>
.get-started-text{
  text-align:center;
  display: flex;
  align-items: center;
  justify-content: center;
  color:#787878db;
  font-size:1.2em;
  position:relative;
  top:20px;
  margin-bottom:50px;

}

.top-text{
  text-align:center;
  display: flex;
  align-items: center;
  justify-content: center;
  color:#787878db;
  font-size:1.2em;
  position:relative;
  top:0px;
  margin-bottom:30px;

}

@font-face {
  font-family: logoFont;
  src: url(/fonts/next.ttf);
}


.logo-title{
    font-family:logoFont;
    font-size:3em;
    color:#be3ebc91;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
